"use client";

import { useEffect, useState } from 'react';
import { useRouter } from 'next/navigation';
import axios from 'axios';
// User ÌÉÄÏûÖÏù¥ '@/types/user'ÏóêÏÑú Ïò¨Î∞îÎ•¥Í≤å import ÎêòÏóàÎã§Í≥† Í∞ÄÏ†ïÌï©ÎãàÎã§.
import { User } from '@/types/user'; 

// üö® API_BASE_URL ÏÉÅÏàòÎ•º Ï†úÍ±∞ÌïòÍ≥† ÏÉÅÎåÄ Í≤ΩÎ°úÎ•º ÏÇ¨Ïö©ÌïòÏó¨ Next.js ÌîÑÎ°ùÏãú ÏÑ§Ï†ïÏùÑ ÌôúÏö©Ìï©ÎãàÎã§.
// Î∞±ÏóîÎìú Í≤ΩÎ°úÍ∞Ä 'http://localhost:3001/api/users'ÎùºÍ≥† Í∞ÄÏ†ïÌïòÎ©¥, 
// ÌîÑÎ°†Ìä∏ÏóêÏÑúÎäî '/api/users'Î°ú ÏöîÏ≤≠ÏùÑ Î≥¥ÎÉÖÎãàÎã§.
const API_PREFIX = '/api/users'; // Next.jsÍ∞Ä 3001Î°ú ÌîÑÎ°ùÏãúÌï¥Ï£ºÍ∏∞Î•º Í∏∞ÎåÄÌï©ÎãàÎã§.

const ProfilePage = () => {
  const [user, setUser] = useState<User | null>(null);
  const [name, setName] = useState('');
  const [contactNumber, setContactNumber] = useState('');
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [isEditing, setIsEditing] = useState(false);
  const [showUpdateSuccess, setShowUpdateSuccess] = useState(false); // Alert ÎåÄÏã† ÏÇ¨Ïö©Ìï† ÏÉÅÌÉú
  const router = useRouter();

  // 1. ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥ Ï°∞Ìöå (GET /api/users/:id)
  useEffect(() => {
    const fetchUser = async () => {
      // üö® [Ï§ëÏöî: LocalStorage ÎåÄÏã† Firebase ÏÇ¨Ïö© Í∂åÏû•]
      // Ïã§Ï†ú ÌîÑÎ°úÎçïÏÖò ÌôòÍ≤ΩÏóêÏÑúÎäî localStorage ÎåÄÏã† ÏïàÏ†ÑÌïú Ïù∏Ï¶ù Î∞è FirestoreÎ•º ÏÇ¨Ïö©Ìï¥Ïïº Ìï©ÎãàÎã§.
      const storedUser = localStorage.getItem('user');
      if (!storedUser) {
        router.push('/auth/login');
        return;
      }

      const { id: userId } = JSON.parse(storedUser);
      if (!userId) {
        router.push('/auth/login');
        return;
      }

      try {
        // ‚úÖ ÏàòÏ†ïÎêú Í≤ΩÎ°ú: ÏÉÅÎåÄ Í≤ΩÎ°ú ÏÇ¨Ïö©. Next.jsÍ∞Ä ÏÑ§Ï†ïÎêú ÌîÑÎ°ùÏãú Í∑úÏπôÏóê Îî∞Îùº 
        // Ïù¥ ÏöîÏ≤≠ÏùÑ 'http://localhost:3001/api/users/{userId}'Î°ú Ï†ÑÎã¨Ìï† Í≤ÉÏûÖÎãàÎã§.
        const response = await axios.get(`${API_PREFIX}/${userId}`);
        
        // Next.js Í∞úÎ∞ú ÏÑúÎ≤ÑÎ•º ÏÇ¨Ïö©ÌïòÍ≥† ÏûàÎã§Î©¥, `response.status !== 200` Í≤ÄÏÇ¨Îäî AxiosÏùò
        // catch Î∏îÎ°ùÏóêÏÑú Ï≤òÎ¶¨ÎêòÎØÄÎ°ú Ï†úÍ±∞ÌïòÍ±∞ÎÇò 2xx ÏΩîÎìúÎßå Í≤ÄÏÇ¨ÌïòÎäî Í≤ÉÏù¥ Ï¢ãÏäµÎãàÎã§.
        
        const data: User = response.data;
        setUser(data);
        setName(data.name);
        setContactNumber(data.contactNumber || '');
        
      } catch (err: any) {
        console.error('Fetch Error (404 Likely):', err);
        // 404/500 ÏóêÎü¨ Ï≤òÎ¶¨
        setError(err.response?.data?.message || 'Failed to fetch user data');
      } finally {
        setLoading(false);
      }
    };

    fetchUser();
  }, [router]);

  // 2. ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥ ÏóÖÎç∞Ïù¥Ìä∏ (PATCH /api/users/:id)
  const handleUpdate = async () => {
    if (!user) return;
    setLoading(true);

    try {
      // ‚úÖ ÏàòÏ†ïÎêú Í≤ΩÎ°ú: ÏÉÅÎåÄ Í≤ΩÎ°ú ÏÇ¨Ïö©
      const response = await axios.patch(`${API_PREFIX}/${user.id}`, {
        name,
        contactNumber,
      });

      const updatedUser: User = response.data;
      
      // LocalStorage ÏóÖÎç∞Ïù¥Ìä∏
      const currentStoredUser = JSON.parse(localStorage.getItem('user') || '{}');
      localStorage.setItem('user', JSON.stringify({
        ...currentStoredUser,
        name: updatedUser.name,
        contactNumber: updatedUser.contactNumber,
      }));

      // UI ÏÉÅÌÉú ÏóÖÎç∞Ïù¥Ìä∏
      setUser(updatedUser);
      setName(updatedUser.name);
      setContactNumber(updatedUser.contactNumber || '');
      setIsEditing(false);
      setShowUpdateSuccess(true); // ÏÑ±Í≥µ ÏïåÎ¶º ÌëúÏãú
      setTimeout(() => setShowUpdateSuccess(false), 3000); // 3Ï¥à ÌõÑ Ïà®ÍπÄ

    } catch (err: any) {
      console.error('Update Error:', err);
      setError(err.response?.data?.message || 'An unknown error occurred during update.');
    } finally {
      setLoading(false);
    }
  };

  // ----------------------------------------------------------------------
  // Î†åÎçîÎßÅ Î°úÏßÅ
  // ----------------------------------------------------------------------
  if (loading) {
    return <div className="flex justify-center items-center h-screen">Loading...</div>;
  }

  // üö® [ÏàòÏ†ï 3] ÏóêÎü¨ Î∞úÏÉù Ïãú UI Í∞úÏÑ†: Failed to fetch user data Î©îÏãúÏßÄ ÌëúÏãú
  if (error) {
    return (
      <div className="flex justify-center items-center h-screen">
        <div className="text-red-600 bg-red-100 p-6 rounded-lg shadow-xl text-center">
          <p className="text-xl font-bold mb-2">Error</p>
          <p className="text-lg">{error}</p>
          <p className="mt-4 text-sm text-red-500">
            (Hint: Î∞±ÏóîÎìú ÏÑúÎ≤ÑÍ∞Ä 'http://localhost:3001'ÏóêÏÑú Ïã§Ìñâ Ï§ëÏù∏ÏßÄ, 
            API Í≤ΩÎ°ú '{API_PREFIX}/{user?.id || 'ID'}'Í∞Ä Ïú†Ìö®ÌïúÏßÄ ÌôïÏù∏ÌïòÏÑ∏Ïöî.)
          </p>
          <button 
             onClick={() => router.push('/')}
             className="mt-3 text-white bg-red-500 hover:bg-red-700 py-1 px-3 rounded-md"
          >
             Go Home
          </button>
        </div>
      </div>
    );
  }

  if (!user) {
    return null; 
  }

  return (
    <div className="container mx-auto px-4 py-8 relative">
      {/* ÏÑ±Í≥µ ÏïåÎ¶º Î™®Îã¨/Î©îÏãúÏßÄ */}
      {showUpdateSuccess && (
        <div className="absolute top-0 right-0 mt-4 mr-4 bg-green-500 text-white py-2 px-4 rounded-lg shadow-lg transition duration-300">
          ÌîÑÎ°úÌïÑÏù¥ ÏÑ±Í≥µÏ†ÅÏúºÎ°ú ÏóÖÎç∞Ïù¥Ìä∏ÎêòÏóàÏäµÎãàÎã§.
        </div>
      )}

      <h1 className="text-3xl font-extrabold text-gray-900 mb-8 text-center">
        {user.name} ÎãòÏùò ÌîÑÎ°úÌïÑ
      </h1>
      <div className="bg-white shadow-2xl border border-gray-100 rounded-xl p-8 max-w-lg mx-auto transform transition duration-500 hover:scale-[1.01]">
        <div className="space-y-6">
          <ProfileField label="Name" isEditing={isEditing} value={name}>
            <input
              type="text"
              value={name}
              onChange={(e) => setName(e.target.value)}
              className="mt-1 shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:ring-2 focus:ring-indigo-500 transition duration-150"
            />
          </ProfileField>

          <ProfileField label="Email" isEditing={false} value={user.email}>
            <p className="text-gray-900 font-medium">{user.email}</p>
          </ProfileField>

          <ProfileField label="Contact Number" isEditing={isEditing} value={contactNumber}>
            <input
              type="text"
              value={contactNumber}
              onChange={(e) => setContactNumber(e.target.value)}
              className="mt-1 shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:ring-2 focus:ring-indigo-500 transition duration-150"
            />
          </ProfileField>

          <ProfileField label="Rating" isEditing={false} value={user.rating}>
            <p className="text-gray-900 font-medium">{user.rating ? `${user.rating.toFixed(1)} / 5.0` : 'No ratings yet'}</p>
          </ProfileField>

          <div className="pt-2">
            <label className="block text-sm font-semibold text-gray-600 mb-2">Identity Verification</label>
            <span
              className={`px-3 py-1 text-xs font-bold leading-tight rounded-full shadow ${
                user.isVerified ? 'bg-green-500 text-white' : 'bg-yellow-500 text-white'
              }`}
            >
              {user.isVerified ? 'Verified' : 'Pending Verification'}
            </span>
          </div>
        </div>
        
        <div className="mt-8 flex justify-end space-x-4 border-t pt-6">
          {isEditing ? (
            <>
              <button
                onClick={handleUpdate}
                disabled={loading}
                className="bg-indigo-600 hover:bg-indigo-700 text-white font-bold py-2 px-4 rounded-lg shadow-md transition duration-150 disabled:opacity-50"
              >
                {loading ? 'Saving...' : 'Save Changes'}
              </button>
              <button
                onClick={() => { setIsEditing(false); setName(user.name); setContactNumber(user.contactNumber || ''); }}
                disabled={loading}
                className="bg-gray-400 hover:bg-gray-500 text-white font-bold py-2 px-4 rounded-lg shadow-md transition duration-150 disabled:opacity-50"
              >
                Cancel
              </button>
            </>
          ) : (
            <button
              onClick={() => setIsEditing(true)}
              className="bg-indigo-500 hover:bg-indigo-600 text-white font-bold py-2 px-6 rounded-lg shadow-md transition duration-150"
            >
              Edit Profile
            </button>
          )}
        </div>
      </div>
    </div>
  );
};

// Ïû¨ÏÇ¨Ïö© Í∞ÄÎä•Ìïú ÌïÑÎìú Ïª¥Ìè¨ÎÑåÌä∏
const ProfileField = ({ label, isEditing, children, value }: { label: string, isEditing: boolean, children: React.ReactNode, value: any }) => (
  <div>
    <label className="block text-sm font-semibold text-gray-600 mb-1">{label}</label>
    {isEditing ? children : (
      <p className="text-gray-900 font-medium bg-gray-50 p-2 rounded-md border border-gray-200">{value}</p>
    )}
  </div>
);

export default ProfilePage;